.post-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}

.post-item {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 5px;
}

.post-item h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.post-item p {
  font-size: 14px;
  color: #555555;
}

.loading {
  text-align: center;
  margin-top: 20px;
  font-size: 16px;
  color: #777777;
}